import React from 'react'
import './mark.sass'
import Symbol from './heart.png'

class Mark extends React.Component {
  render() {
    return(
      <div className="mark">
        <div className="line"></div>
        <img src={Symbol}/>
        <div className="line"></div>
      </div>
    )
  }
}

export default Mark;


